import React, { createContext, useReducer } from 'react';
import { CartReducer, sumItems} from './CartReducer';

export const CartContext = createContext();

const storage = sessionStorage.getItem('RFQList') ? JSON.parse(sessionStorage.getItem('RFQList')) : [];
const initialState = { cartItems: storage, ...sumItems(storage), checkout: false };

const CartContextProvider = ({children}) => {

    const [state, dispatch] = useReducer(CartReducer, initialState)

    const increase = payload => {
        console.log(payload)
        dispatch({type: 'INCREASE', payload})
    }

    const setQuantity = (payload,quantity) => {
        console.log(payload)
        dispatch({type: 'setQuantity', payload, quantity})
    }

    const decrease = payload => {
        dispatch({type: 'DECREASE', payload})
    }

    window.addProductWindow = function addProductWindow(payload){
        dispatch({type: 'ADD_ITEM', payload})
    }

    const addProduct = payload => {
        dispatch({type: 'ADD_ITEM', payload})
    }
    const removeProduct = payload => {
        dispatch({type: 'REMOVE_ITEM', payload})
    }

    const clearCart = () => {
        window.$("img").removeClass("ordered");
        dispatch({type: 'CLEAR'})
    }

    const handleCheckout = () => {
        console.log('CHECKOUT', state);
        window.$("img").removeClass("ordered");
        // Add headers
        var headers = {
            description: "DESCRIPTION",
            part: "PART NO.",
            quantity: "QTY",
            price: "PRICE",
            remark: "REMARKS"
        };
        var itemsFormatted = [];
        // format the data
        JSON.parse(sessionStorage.getItem('RFQList')).forEach((item) => {
            itemsFormatted.push({
                description: item.description.replace(/,/g, ''),// remove commas to avoid errors,
                part: item.part,
                quantity: item.quantity,
                price: item.price,
                remark: item.remarks
            });
        });
        var today = new Date();
        var date = ('0' + today.getDate()).slice(-2)
            + ('0' + (today.getMonth()+1)).slice(-2)
            + today.getFullYear();
        var time = ('0' + today.getHours()).slice(-2)
            + ('0' + (today.getMinutes())).slice(-2)
            + ('0' + (today.getSeconds())).slice(-2);
        var fileTitle = 'order_'+date+'_'+ time;
        window.exportCSVFile(headers, itemsFormatted, fileTitle);
        dispatch({type: 'CHECKOUT'})
    }

    const contextValues = {
        removeProduct,
        addProduct,
        increase,
        decrease,
        clearCart,
        handleCheckout,
        setQuantity,
        ...state
    } 

    return ( 
        <CartContext.Provider value={contextValues} >
            { children }
        </CartContext.Provider>
     );
}
 
export default CartContextProvider;